export function isIssueLabelEvent(context) {
    return context.eventName === "issues.labeled" || context.eventName === "issues.unlabeled";
}
export function isIssueOpenedEvent(context) {
    return context.eventName === "issues.opened";
}
export function isIssueCommentEvent(context) {
    return "issue" in context.payload;
}
export function isPushEvent(context) {
    return context.eventName === "push";
}
